import React, { useEffect } from "react";
import {useStore} from '@/stores';
import { Card, Heading } from '@/components';
import css from './QuickstartPage.module.scss';
import cx from 'classnames';
import { changeTitle } from '../routes';
import { observer } from "mobx-react";
import { useTranslation } from "react-i18next";
import { Logo } from "@/components/layout/Header/partials/Logo";
import { QuickstartConfig } from "@/views/Applications/Partials/QuickstartConfig";

const QuickstartPage = observer(() => {
  changeTitle('Quick start')
  const { globalStore } = useStore();
  const { t } = useTranslation();

  useEffect(() => {
    globalStore.toggleQuickstartView(true);
  }, [])

  const getExitUrl = () => {
    return globalStore.serverUrl;
  };

  return (
    <div className={css.root}>
      <Card className={css.card} classes={{ content: css.cardFlex }}>
          <div className={cx(css.alignCenter, css.pointer)} onClick={() => { window.location.reload(); }}>
            <Logo withLink={false} forceSmall={true} />
            <Heading title={t('quickstart.title')} />
          </div>
        
        <QuickstartConfig standalone={true} />

        <div className={css.footnote}>
          <div className={css.flexGrow}>
            <small dangerouslySetInnerHTML={{ __html: t('quickstart.contactUs') }} />
          </div>
          <div>
            <small dangerouslySetInnerHTML={{ __html: t('quickstart.exit', {url: getExitUrl() }) }} />
          </div>
        </div>
      </Card>
    </div>
  );
});

export default QuickstartPage